/*------------------------------------*\
  #Animation
\*------------------------------------*/

/* Fade In */
@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeInFadeOut {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
@-webkit-keyframes fadeInFadeOut {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

/* loading*/
@keyframes loading {
    0% {
        background-position: -180px;
    }

    50% {
        background-position: 380px;
    }
    100% {
        background-position: -180px;
    }
}
@-webkit-keyframes loading {
    0% {
        background-position: -180px;
    }

    50% {
        background-position: 380px;
    }
    100% {
        background-position: -180px;
    }
}
